<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :model="model"
        icon="mdi-cog-transfer"
        :entity-name=" $t('分区打印设置')"
      />
      <v-card>
        <v-toolbar
          dense
          color="primary"
          dark
        >
          <v-toolbar-title>{{ $t('设备分布') }}</v-toolbar-title>
          <v-spacer/>
          <v-btn
            text
            @click="clearAll"
          >
            {{ $t('清空') }}
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-data-table
            dense
            hide-default-footer
            :headers="headers"
            :items-per-page="100"
            :items="tableData"
          >
            <template
              v-for="(s, idx) in sectionHeaders"
              v-slot:[`item.${s.name}`]="{ item }"
            >
              <v-checkbox
                :key="idx"
                :disabled="loading"
                :input-value="item[s.name]"
                @click="updateSection(item, s.name, s.id)"
              />
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>

  import PrinterGroup from '../../model/PrinterGroup'
  import DeviceList from '../../model/DeviceList'
  import hillo from 'hillo'
  import { IkDataTable } from 'metaflow-js'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: PrinterGroup,
        displayData: [],
        loading: false,
        sectionHeaders: [],
        fixedHeader: [
          {
            text: 'name',
            value: 'name',
          },
          {
            text: 'sn',
            value: 'sn',
          },
        ],
      })
    },
    computed: {
      headers () {
        return this.fixedHeader.concat(this.sectionHeaders)
      },
      tableData () {
        const tableData = []
        this.displayData.forEach(d => {
          d.name = d.printerName
          this.sectionHeaders.forEach(s => {
            if (d.groupId) {
              d[s.name] = (d.groupId.indexOf(s.id) > -1)
            }
          })
          tableData.push(d)
        })
        return tableData
      },
    },
    mounted () {
      this.loadData()
    },
    methods: {
      async updateSection (item, sName, sId) {
        this.loading = true
        item[sName] = !item[sName]
        if (item[sName]) {
          item.groupId.push(sId)
        } else {
          item.groupId.splice(item.groupId.indexOf(sId), 1)
        }
        item.groupIds = JSON.stringify(item.groupId)
        await hillo.post('Printer.php?op=edit', item)
        this.loading = false
      },
      clearAll () {
        this.loading = true
        this.displayData.forEach(item => {
          // sitem[sName] = !item[sName]
          item.groupId = []
          item.groupIds = JSON.stringify(item.groupId)
          hillo.post('Printer.php?op=edit', item)
        })
        this.loading = false
      },
      async loadData () {
        this.sectionHeaders = (await PrinterGroup.getList()).map(a => {
          a.text = a.name
          a.value = a.name
          return a
        })
        this.displayData = (await DeviceList.getList())
        console.log(this.displayData, 'display')
      },
    },
  }
</script>
